
let latency = 200;

let reportHistory = [
  {
    "name": "Mobile World Congress",
    "rsids": [
      "martcom",
      "getusprod",
      "mainprod"
    ]
  },
  {
    "name": "Finance",
    "rsids": [
      "bank1",
      "bank2",
      "bank3",
      "bank4"
    ]
  }
];


export class ModalAPI {
  isRequesting = false;

  getReportHistory() {
    this.isRequesting = true;
    return new Promise(resolve => {
      setTimeout(() => {
        resolve(JSON.parse(JSON.stringify(reportHistory)));
        this.isRequesting = false;
      }, latency);
    });
  }
}